/* //Seccion Beneficio Benefits */
.benefits {
  position: relative;
}

.svg-benefits {
  position: absolute;
  left: -36px;
  top: -35px;
  width: 185px;
}
.container-benefits {
  padding: 60px 30px 0 30px;
  position: inherit;
}
.benefits-titulo {
  position: relative;
  font-family: var(--font-family);
  font-size: 2.5rem;
  margin-left: 9%;
}
.benefits-titulo p {
  color: #222222;
  position: relative;
}
.benefits-titulo span {
  display: block;
  color: #ebfdff;
}

.benefits-content {
  display: grid;
  grid-template-columns: repeat(3, minmax(150px, 220px));
  padding: 100px 0;
  color: var(--color-text);
  gap: 40px;
  justify-content: center;
}
.benefits-content_card-high {
  position: relative;
  top: -30px;
}

.benefits-content_card {
  border: solid 3px #7166c000;
  width: 100%;
  height: 100%;
  padding: 15px;
  border-radius: 8px;
  box-shadow: -3px -3px 4px 0px #ffffff1c, 4px 4px 4px 2px #00000026,
    inset 5px 5px 7px 2px #00000033, inset -3px -3px 5px 0px #ffffff1c;
  transition: 0.2s;
}
.benefits-content_card-container {
  text-align: center;
  background: var(--gradients-bg-2);
  padding: 40px 20px;
  border-radius: 4px;
  box-shadow: 2px 2px 4px 4px #0c0b1473;
  cursor: pointer;
  font-size: 14px;
  color: var(--gradients-bg-text-2);
  position: relative;
}
.benefits-content_card:hover {
  transform: scale(1.1);
}
.benefits-content_card img {
  /* width: 100px; */
  height: 50px;
  margin-bottom: 1rem;
}
.benefits-content_card .titulo {
  font-family: var(--font-family);
}

.benefits-content_card p {
  font-family: var(--font-family-1);
  margin-top: 1rem;
}

@media screen and (max-width: 700px) {
  .container-benefits {
    padding: 60px 20px 0 20px;
  }
  .benefits-content {
    grid-template-columns: repeat(1, minmax(150px, 220px));
  }
  .benefits-content_card-high {
    top: 0;
  }
}
