.SocialRF {
  position: fixed;
  bottom: 34px;
  left: 35px;
  z-index: 2;
}
.SocialRF-content {
}
.SocialRF-class {
  color: var(--color-icon);
  margin-top: 10px;
  font-size: 28px;
  transition: 0.3s;
  cursor: pointer;
}
.SocialRF-class:hover {
  transform: scale(1.2);
}

@media screen and (max-width: 700px) {
  .SocialRF {
    bottom: 20px;
    left: 15px;
  }
  .SocialRF-class {
    font-size: 24px;
  }
}

